import React, { useState } from 'react';
import {Button, message, Space, Steps, theme} from 'antd';
import {UserOutlined,LoadingOutlined,SmileOutlined,  SolutionOutlined} from "@ant-design/icons";
import AuthForm from "../AuthForm/auth-form";
import PhoneNumberInput from "../AuthForm/auth-number_send_form";


const steps = [
    {
        title: '',
        status: 'finish',
        icon: <UserOutlined />,
    },
    {
        title: '',
        status: 'finish',
        icon: <SolutionOutlined />,
    },
    {
        title: '',
        status: 'wait',
        icon: <SmileOutlined />,
    },
]
const StepAuth = () => {
    const { token } = theme.useToken();
    const [current, setCurrent] = useState(0);
    const [isNextButtonActive, setNextButton] = useState(false);
    const [isDoneButtonActive, setDoneButton] = useState(false);
    const [isPreviousButtonActive, setPrevious] = useState(false);



    const next = () => {
        setCurrent(current + 1);
    };

    const prev = () => {
        setCurrent(current - 1);
    };

    const items = steps.map((item) => ({ key: item.title,icon: item.icon, title: item.title }));

    React.useEffect(()=> {
        setNextButton(current > 0 && current < steps.length - 1)
        setDoneButton(current === steps.length - 1)
        setPrevious(current > 0)

    }, [current])


    return (
        <>
            <Steps
                current={current}
                items={items}

            />

            {
                current === 0 &&
                <PhoneNumberInput/>
            }
            {
                current === 1 && <AuthForm/>
            }
            {
                current === 2 && <div>Done</div>
            }
            {isNextButtonActive && (
                <Button type="primary" onClick={() => next()}>
                    Next
                </Button>
            )}
            { isDoneButtonActive && (
                <Button type="primary" onClick={() => message.success('Processing complete!')}>
                    Done
                </Button>
            )}
            {isPreviousButtonActive && (
                <Button style={{ margin: '0 8px' }} onClick={() => prev()}>
                    Previous
                </Button>
            )}
        </>
    );
};

export default StepAuth;