



// ============================= ACTION =============================================================



// ============================= STATE =============================================================

export enum ActionTypes {
    DRAWNER_OPENED = "DRAWNER_OPENED",
    DRAWNER_CLOSED = "DRAWNER_CLOSED"
}

interface InitialState{
    profileStateVisible: boolean,
}

const initialState: InitialState = {
    profileStateVisible: false
}

export interface DRAWNER_OPENED_ACTION_OPEN {
    type: ActionTypes.DRAWNER_OPENED
}

export interface DRAWNER_OPENED_ACTION_CLOSE {
    type: ActionTypes.DRAWNER_CLOSED
}


export type PrifileAction = DRAWNER_OPENED_ACTION_CLOSE | DRAWNER_OPENED_ACTION_OPEN

// ==========================================================================================

export const ProfileReducer = (state= initialState, action: PrifileAction): InitialState => {
    switch (action.type) {
        case ActionTypes.DRAWNER_OPENED:
            return {profileStateVisible: true}
        case ActionTypes.DRAWNER_CLOSED:
            return {profileStateVisible: false}
        default:
            return {profileStateVisible: false}
    }
}