import {Input, InputNumber, Form, Select, Space, Button} from 'antd';
import React, {JSX} from 'react';
import login from "../../pages/Login";
import {get_country_codes_of_number, get_location_city} from "../../api/config_service/ApiService";
// import countries from './countries.json';

const {Option} = Select;


