function PhoneNumberInput() {
    const [CodeNumber, SetCodeInput] = React.useState(0)
    const [isDisabledInput, SetDisabledInput] = React.useState(true)
    const [CountressCode, SetCountressCode] = React.useState([])
    const [isDisabledPlaceholder, SetDisabledPlaceholder] = React.useState(false)
    const [form] = Form.useForm();
    const [number, SetNumber] = React.useState("")

    React.useEffect(() => {
        get_location_city().then(r => console.log(r))
        get_country_codes_of_number().then(json => SetCountressCode(json))
    }, [])

    const onFinish = () => {
        console.log('Received values of form: ', number);
    };

    return (
        <>
            <Form
                form={form}
                name="register"
                onFinish={onFinish}
                scrollToFirstError
                layout="vertical"
            >
                <Form.Item
                    label={"Choose your country"}
                >
                    <Select
                        optionLabelProp="label"
                        onSelect={(value, option) => {
                            SetCodeInput(option?.object?.dial_code)
                            SetDisabledInput(false)
                        }}
                        showSearch
                        filterSort={(optionA, optionB) =>
                            (optionA?.name ?? '').toLowerCase().localeCompare((optionB?.name ?? '').toLowerCase())
                        }
                    >
                        {CountressCode?.map(country => (
                            <Option label={country.name} key={country.code} object={country} value={country.dial_code} data-name={country.name}>
                                <div style={{display: "flex", justifyContent: "space-between"}}>
                                    <span role="img" aria-label={country.name}>🇰🇬</span>
                                    <span role={"tab"} style={{fontWeight: "bold"}}>{country.name}</span>
                                    <span role={"tab"} style={{fontWeight: "initial"}}>{country.dial_code}</span>
                                </div>
                            </Option>
                        ))}
                    </Select>
                </Form.Item>

                <Form.Item
                    name="phone"
                    label="Phone Number"
                    rules={[
                        {required: true, message: 'Please enter your phone number'},
                    ]}
                >
                    <Input prefix={CodeNumber} onChange={(number)=> SetNumber(number.target.value)} disabled={isDisabledInput}/>

                </Form.Item>

                <Button type="primary" onClick={() => console.log(CodeNumber, number)}>
                    Send code
                </Button>
            </Form>
        </>

    );
}

export default PhoneNumberInput
