

export const get_location_city = async () => {
    return await fetch('http://127.0.0.1:8000/config/get-location/', {method: "get"})
        .then(response => response.json())
}


export const get_country_codes_of_number = async () => {
    return await fetch("/CountryCodes.json")
        .then(response => response.json())
}